ul {
  list-style-type: none;
  padding-inline-start: 0px;
}

li {
  line-height: 2.5;
}

.main-div {
  margin: 15px;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.column-photo {
  display: flex;
  flex-direction: column;
  flex: 0 0 60em;
}      

.column-menu {
  display: flex;
  flex-direction: column;
  flex: 0 0 25em;
}  

.name {
  font-weight: 700;
  font-size: x-large;
}

.story {
  display: flex;
  flex-direction: column;
  margin-top: 5%;
}

.storyTitle {
  color: #424242;
  text-align: center;
  font-weight: 100;
  letter-spacing: 2px;
}

.paragraphs {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.paragraphsFont {
  font-weight: 400;
  letter-spacing: 1.5px;
  color: #424242;
}

a, a:link, a:visited, a:focus, a:hover, a:active{
  color: #424242;
  text-decoration: none; 
  cursor: pointer;
}

.icon-items {
  display: flex;
  justify-content: flex-start;
}

.icon-item {
  margin-right: 3%;
}

.app-left-menu {
  margin-top: 15%;
}

header {
  margin-left: 10%;
  margin-top: 25%;
  margin-bottom: 5%;
}

footer {
  text-align: center;
}

.main-image {
  margin-top: 5%;
} 

.App-link {
  color: #61dafb;
}
